
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import { mapState ,mapGetters,mapMutations} from 'vuex';
export default {
name: 'app',
components: {
XHeader,
Actionsheet
},

created:function() {

var loginStatus=window.localStorage.getItem('loginStatus');

if(loginStatus){

this.mutationsLogin();
this.mutationsLoginname(window.localStorage.getItem('loginname'));
this.mutationsAccesstoken(window.localStorage.getItem('accesstoken'));
}
},

computed: {

...mapGetters(['MoreMenus']),

...mapState(['Topic','UnLoginedinMoreMenus','LoginedinMoreMenus','login']),

isShowMoreMenus:{
get:function(){
return this.$store.state.isShowMoreMenus;
},
set:function(nval){

this.$store.commit('mutationsIsShowMoreMenus',nval);
}
}

},
methods: {
...mapMutations(['mutationsLogin','mutationsLoginname','mutationsAccesstoken']),

ShowmoreMenu(){


this.isShowMoreMenus=true;
},
loginlink(menuKey, menuItem){

this.$router.push('/'+menuKey);
}
}
}



