import Vue from 'vue';
import Vuex from 'vuex';
import mutations from "./mutations.js";
import actions from "./actions.js";
//
Vue.use(Vuex)

//store
const store=new Vuex.Store({
    //()
    state:{
        //
        Topic:[
            {name:'all',desc:''},
            {name:'good',desc:''},
            {name:'share',desc:''},
            {name:'ask',desc:''},
            {name:'job',desc:''},
            {name:'dev',desc:''}
          ],
        //   
          UnLoginedinMoreMenus:
            {login:""}
          ,
          //   
          LoginedinMoreMenus:
            {
              center:"",
              msg:"",
              loginout:""
            }
          ,
          //   
          login:false,
          loginname:'',
          accesstoken:'',
          //   actionsheet
          isShowMoreMenus:false
        },
    
    //
    getters:{
        MoreMenus:function(state){
            if(state.login){
              // 
              return state.LoginedinMoreMenus;
            }
              return state.UnLoginedinMoreMenus;
            }
    },
    mutations,
    actions
});


  //
  export default store;


