
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'

import {mapMutations} from 'vuex'
export default {
name:'Login',
components:{
XInput,
XButton,
Group
},
data:function(){
return{
accesstoken:''
}
},
methods: {
...mapMutations(['mutationsLogin','mutationsLoginname','mutationsAccesstoken']),
dologin(){
var app=this;
this.$http.post('/accesstoken',{
accesstoken:this.accesstoken

}).then(function(response){
if(response,response.data,response.data.success){

app.mutationsLogin();
app.mutationsLoginname(response.data.loginname);
app.mutationsAccesstoken(app.accesstoken);


window.localStorage.setItem('loginStatus',true);
window.localStorage.setItem('loginname',response.data.loginname);
window.localStorage.setItem('accesstoken',app.accesstoken);

app.$router.push('/user/'+response.data.loginname);
}
});

}
},
}



